#history {
  display: flex;
  align-items: center;
}

#history h3 {
  background-color: green;
  padding: 10px;
  color: #fff;
  margin: 20px;
  border-radius: 5px;
}

#history button {
  color: #333;
  background: none;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
